@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@200;300;400;800;900&display=swap);
.toolbar {
    background-color: #6a53e4;
    height: 100vh;
    width: 50px;
}

.toolbar-items-section {
    position: absolute;
    top: 30%;
}

.toolbar-item-img {
    margin-left: 7px;
    height: 40px; /* make same height as active indicator to avoid jumping around */
    width: 40px; /* make all images the same width */
    object-fit: none; /* but don't adjust the image size/aspect ratio just the frame size */
}

.toolbar-active-indicator {
    margin-right: -4px; /* counter the width of the indicator */
}

.toolbar-item {
    margin-bottom: 100px;
}

.splitview-detail-column {
    border-left: 1px lightgray solid;
}

.login-background {
    height: 100vh;
    background: url("/meeting-room-schedule-visualisation/static/media/Login background.c0ce601e.jpg") no-repeat center;
    background-size: cover;
}

.login-header-text {
    margin-top: 95px;
    color: #393a6d;
    font-weight: 700;
}

.login-information-text {
    font-size: medium;
    margin-top: 25px;
    color: lightgray;
}

.login-button:before {
    content: url(/meeting-room-schedule-visualisation/static/media/Outlook.107882b1.svg);
    display: inline-block;
    width: 30px;
    left: 10px;
    top: 13px;
    position: absolute;
}

.login-button {
    border-radius: 0.5rem !important;
    position: relative;
    padding-top: 15px !important;
    padding-bottom: 15px !important;
    padding-left: 55px !important;
    margin-top: 35px;
    background-color: #6a53e4 !important;
    color: white !important;
    font-size: large !important;
}

.login-copyright-text {
    position: absolute;
    bottom: 20px;
    font-size: small;
    color: lightgray;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
}


.login-popover {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 500px;
    height: 500px;
    margin: -250px 0 0 -250px; /* apply negative top and left margins to truly center the element */
    background-color: white;
    border-radius: 25px;
}
.meetings-overview-scroll-container {
    height: 100vh;
    overflow: scroll;
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
}

/* chrome */
.meetings-overview-scroll-container::-webkit-scrollbar {
    display: none;
}

.meetings-overview-meeting-attendees {
    display: -webkit-inline-flex;
    display: inline-flex;
    list-style: none;
    padding-left: 20px;
    padding-right: 20px;
    width: 100%;
    position: absolute;
    bottom: 10px;
}

.meetings-overview-meeting-attendees > li {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    overflow: hidden;
    margin-right: 10px;
}

.meetings-overview-meeting-attendees > li:last-child {
    margin-right: 0;
}

.meetings-overview-meeting-attendees > li > img {
    height: 100%;
}

.meetings-overview-meetings {
    list-style: none;
    padding: 15px;
}

.meetings-overview-meetings > li {
    border: 1px solid lightgray;
    border-radius: 5px;
    position: relative;
    height: 150px;
    overflow: hidden;
    margin-bottom: 20px;
}

.meetings-overview-meetings > li:last-child {
    margin-bottom: 0;
}

.meetings-overview-meeting-text {
    padding: 20px;
}

.meetings-overview-meeting-text h6 {
    font-weight: 600;
    margin-bottom: 4px;
    margin-right: 45px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}

.meetings-overview-meeting-icon {
    border-radius: 50%;
    width: 40px;
    height: 40px;
    float: right;
    margin-top: -20px;
}

.meetings-overview-meeting-icon > img {
    margin: auto;
    display: block;
    top: 50%;
    position: relative;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}

.meetings-overview-header-text {
    padding-bottom: 10px;
}


.meetings-overview-heading {
    margin-top: 15px;
    margin-left: 15px;
}

.meetings-overview-meeting-color-tab {
    width: 7px;
    height: 100%;
    position: absolute;
}

.meetings-overview-empty-view {
    position: relative;
    text-align: center;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}

.meetings-overview-empty-view-header {
    color: gray;
}

.meetings-overview-empty-view-message {
    color: gray;
}

.meetings-overview-loading-view {
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.meetings-overview-loading-view-spinner {
    border: 8px solid lightgray;
    border-top: 8px solid #6a53e4;
    border-radius: 50%;
    width: 60px;
    height: 60px;
    -webkit-animation: spin 1.5s linear infinite;
            animation: spin 1.5s linear infinite;
    margin: auto;
}

@-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


html, body {
    font-family: 'Nunito Sans', sans-serif;
}
